<template>
  <div id="single-blog" >
    <h2>博客详情</h2>
    <div class="show" v-for="onlyBlog in blog" :key="onlyBlog.ID" >
      <div v-if="onlyBlog.ID === id">
        <p>标题: {{ onlyBlog.title }}</p>
        <p>内容: {{ onlyBlog.content }}</p>
        <p>分类: {{ onlyBlog.category }}</p>
        <p>作者: {{ onlyBlog.author }}</p>
      </div>     
    </div>
    <button>
      <router-link to="/">返回</router-link>
    </button>  
  </div> 
</template>

<script>
export default {
  name:'single-blog',
  data(){
    return {
      id:this.$route.query.id,
      blog:{},//接受查询出来的结果 
    }
  },
  mounted(){
    this.showSingleBlog()
  },
  methods:{
    showSingleBlog(){
      const query = this.Bmob.Query("blogs");
			query.find().then(res => {
        this.blog = res;
        console.log('这是singlepage');
        console.log(this.blog.length);
			});    
    },
  }
}
</script>

<style>
#single-blog{
  position: relative;
  margin: 0 auto;
  max-width: 800px;
  padding: 30px;
  margin-top: 30px;
  background:rgb(235, 224, 224);
  border: 1px dotted #aaa;
}
#single-blog h2{
  text-align: center;
}
#single-blog .show p{
  font-size: 20px;
  margin-top: 10px;
}
#single-blog button {
  position: absolute;
  right:30px;
  bottom: 20px;
  background: coral;
  margin-right: 10px;
  margin-top: 30px;
}
#single-blog button a{
  text-decoration: none;
  font-size: 20px;
  color:white;
}
</style>